(function () {
  'use strict';

  angular
    .module('fishingKingMobile')
    .controller('UserController', UserController);

  /** @ngInject */
  function UserController(jQ, $log, player, localStorage, payment) {
    var vm = this;
    vm.changePic = false;
    vm.pictureNo = null;
    vm.photoUrl = 'dpblank.png';
    vm.fullPhotoUrl = "assets/images/portrait/dpblank.png";
    vm.bankTypeName = '';

    player.getPlayerPartner(function () {
      var authenticatedData = angular.fromJson(localStorage.getData());

      vm.userData = authenticatedData.player;
      if (vm.userData.bankAccount) {
        vm.userData.bankAccount = vm.userData.bankAccount.slice(-3);
      } else {
        vm.userData.bankAccount = '';
      }
      if (vm.userData.photoUrl && vm.userData.photoUrl != '') {
        vm.photoUrl = vm.userData.photoUrl;
        vm.fullPhotoUrl = "assets/images/portrait/" + vm.userData.photoUrl;
      }else{
        vm.fullPhotoUrl = "/assets/images/portrait/1.jpg";
      }
      getBankTypeList();
    });

    vm.selectPic = function (no, fileName) {
      jQ('.userImage div').removeClass('chosenPic')
      vm.pictureName = fileName;
      jQ('.dp' + no).addClass('chosenPic');
    }
    vm.changeDisplayPic = function () {
      vm.changePic = true;
    }
    vm.confirmDisplayPic = function () {
      $log.info(vm.pictureName + 'Update to this picture');
      player.emit('updatePhotoUrl', {photoUrl: vm.pictureName})
      player.on('updatePhotoUrl', function (data) {

        if (data.status == 200) {
          // vm.userData.photoUrl = vm.pictureName;
          vm.reloadDisplayImage();
          $log.info('photo Update Success');
        } else {
          $log.info('photo Update Failed');
        }
        vm.changePic = false;
      })
    }
    vm.reloadDisplayImage = function () {
      if (vm.pictureName) {
        vm.fullPhotoUrl = "assets/images/portrait/" + vm.pictureName;
      } else {
        vm.fullPhotoUrl = "assets/images/portrait/dpblank.png";
      }

    }

    function getBankTypeList() {
      var requestId = Math.floor(Math.random() * 10000) + "" + new Date().getTime();
      payment.emit('getBankTypeList', {}, 99, requestId);
      payment.on('getBankTypeList', function (data) {
        data.data.forEach(function (item) {
          if (item && item.bankTypeId && item.bankTypeId == vm.userData.bankName) {
            vm.userBank = item.name;
          }
        })
      }, {requestId: requestId})
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('fishingKingMobile')
    .controller('TransferController', TransferController);

  /** @ngInject */
  function TransferController(jQ, $log, player, localStorage, payment, game) {
    var vm = this;
    vm.transferType ="0";
    var lstore = angular.fromJson(localStorage.getData())
    vm.providerList = [];
    // hardcode EBETFISH provider
    vm.provider = 69;
    vm.gameStatus = {
        0: "",
        1: "",
        2: "",
        3: "",
        4: "",
        5: ""
    };
    //GameProvider State
    if(lstore){
      player.emit('get',{playerId:lstore.player.playerId},99);
      payment.emit('getBankTypeList', {},99 );
    }
    player.on('get',function(data){
      $log.info(data);
      vm.credit = (data.data.validCredit+data.data.lockedCredit).toFixed(2);
    });

    vm.transferCredit = function(){
      var ans = confirm("");
      if(ans){
        if(vm.transferType==0){
          //Transfer of credits from player account to a providers game credit account (CP)
          game.emit('transferToProvider',{
              playerId:lstore.player.playerId,
              providerId: vm.provider
          })
        }else if(vm.transferType==1){
          //Transfer of credits from providers game credit account to player account (CP)
          game.emit('transferFromProvider',{
              playerId:lstore.player.playerId,
              providerId: vm.provider
          })
        }
      }

    }
    game.on('transferToProvider',function(data){
      if(data.status==200){
        alert('');
      }else{
        if(data.errorMessage){
          alert(data.errorMessage);
        }
      }
      vm.getUserCredit();
      vm.refreshGameProviders();
    })
    game.on('transferFromProvider',function(data){

      if(data.status==200){
        alert('');
      }else{
        if(data.errorMessage){
          alert(data.errorMessage);
        }
      }

      vm.getUserCredit();
      vm.refreshGameProviders();
    })
    vm.getStatusName = function(status){
      if(vm.gameStatus[status]){
        return vm.gameStatus[status];
      }else{
        return status;
      }

    }

    vm.getUserCredit = function(){
      player.emit('get',{playerId:lstore.player.playerId},99);
      player.on('get',function(data){
        vm.credit = (data.data.validCredit+data.data.lockedCredit).toFixed(2);
      })
    }
    vm.chosenProvider = function(providerId){
      jQ("input:radio").attr("checked", false);
      vm.provider = providerId;
    }
    vm.refreshGameProviders = function(){
      for(var item in vm.providerList){
        game.emit('getGameProviderCredit', {providerId:vm.providerList[item].providerId})
      }
    }

    game.emit('getProviderList',{playerId:lstore.player.playerId})

    game.on('getProviderList', function(data){
      if(data){
        data.data.forEach(function(pr){
          var provider = {};
          provider.providerId = pr.providerId;
          provider.name = pr.name;
          provider.nickname = pr.nickName ? pr.nickName.replace('AG','').replace('2','') : pr.name;
          provider.status = pr.status;
          provider.statusName = vm.getStatusName(pr.status);
          provider.credit = 0;
          vm.providerList.push(provider);
        })
      }
      vm.refreshGameProviders();
    });

    game.on('getGameProviderCredit',function(data){

      if(data.data){
        var result = data.data;
        vm.providerList.forEach(function(provider){
          if(provider.providerId == result.providerId){
            provider.credit = result.credit;
          }
        })
      }
    })

  }
})();

