(function () {
  'use strict';

  angular
    .module('fishingKingMobile')
    .controller('LoginController', LoginController);

  /** @ngInject */

  function LoginController($log, $window, $document, customerService, jQ, localStorage, player, $state, $rootScope, $interval, platform, platformId, $http) {
    var vm = this;
    vm.platformId = platformId;
    function login() {
      var requestId = Math.floor(Math.random() * 10000) + "" + new Date().getTime();
      var params = {};
      var funcName = 'login';
      if (vm.name && vm.password) {
        var params = {
          name: vm.name,
          password: vm.password,
          platformId: vm.platformId
        };
        funcName = 'login';
      }
      else {
        alert("")
        return;
      }
      player.emit(funcName, params, 99, requestId);
      player.on(funcName, function (respData) {
        if (respData && respData.status == 200 && respData.data) {
          var authenticatedData = {
            token: respData.token,
            player: respData.data,
          };
          localStorage.setData(angular.toJson(authenticatedData));
          $rootScope.loggedOn = true;
          $rootScope.validCredit = authenticatedData.player.validCredit;
          $rootScope.lockedCredit = authenticatedData.player.lockedCredit;
          $rootScope.checkHasNewMail();

          player.emit("getCreditDetail", {});

          vm.message = "";
          jQ('#loginModal').modal('show');
          jQ('#loginModal').on('hidden.bs.modal', function () {
            $state.go("home");
          })
          $log.debug("");
        } else {
          localStorage.removeData();
          $rootScope.loggedOn = false;
          $rootScope.validCredit = 0;
          $rootScope.lockedCredit = 0;
          $rootScope.totalCredit = 0;

          vm.message = respData.errorMessage;
          jQ('#loginModal').modal('show');
          $log.debug("");
        }
      }, {requestId: requestId});


      player.on('loginPlayerPartner', function (respData) {
        if (respData && respData.status == 200 && respData.data && respData.data.length == 2 && respData.data[0] && respData.data[1]) {
          var authenticatedData = {
            token: respData.token,
            player: respData.data[0],
            partner: respData.data[1]
          };
          localStorage.setData(angular.toJson(authenticatedData));
          $rootScope.loggedOn = true;
          $rootScope.validCredit = authenticatedData.player.validCredit;
          $rootScope.lockedCredit = authenticatedData.player.lockedCredit;

          player.emit("getCreditDetail", {});

          vm.message = "";
          jQ('#loginModal').modal('show');
          jQ('#loginModal').on('hidden.bs.modal', function () {
            $state.go("home");
          })
          $log.debug("");
        } else {
          localStorage.removeData();
          $rootScope.loggedOn = false;
          $rootScope.validCredit = 0;
          $rootScope.lockedCredit = 0;
          $rootScope.totalCredit = 0;

          vm.message = respData.errorMessage;
          jQ('#loginModal').modal('show');
          $log.debug("");
        }
      }, {requestId: requestId});


    }

    vm.login = login;

    function openWebChat() {
      var e = customerService,//"https://www.chat800.com/chat/chatClient/chatbox.jsp?companyID=267&configID=26",
        t = encodeURIComponent($document.URL),
        n = encodeURIComponent($document.referrer);
      $window.open(e + "&enterurl=" + t + "&pagereferrer=" + n)
    }

    vm.openWebChat = openWebChat;

    function getSMSCode() {
      if (!vm.mobile) {
        $window.alert("");
        return;
      }
      if (vm.countDown && vm.countDown > 0) {
        return;
      }
      player.emit('getSMSCode', {phoneNumber: vm.mobile, platformId: platformId});
    }

    vm.getSMSCode = getSMSCode;

    vm.getWechatAuth = function(){


        $http.get("https://api.weixin.qq.com/cgi-bin/token?grant_type=client_credential&appid="+appId+"&secret="+secret).then(function(response) {
        });
    }


    player.on('getSMSCode', function (data) {
      if (data && data.status && data.status == 200) {
        vm.countDown = 60;
        vm.intervalPromise = $interval(function () {
          if (vm.countDown && vm.countDown > 0) {
            vm.countDown--;
          }
          else {
            $interval.cancel(vm.intervalPromise);
          }
          $log.debug(vm.countDown);
        }, 1000)
      }
      else {
        if (data.errorMessage) {
          alert(data.errorMessage);
          vm.countDown = 60;
          vm.intervalPromise = $interval(function () {
            if (vm.countDown && vm.countDown > 0) {
              vm.countDown--;
            }
            else {
              $interval.cancel(vm.intervalPromise);
            }
            $log.debug(vm.countDown);
          }, 1000)
        }
      }
    });

    player.on("getCreditDetail", function (data) {
      $rootScope.totalCredit = 0;
      if (data && data.status == "200" && data.data) {
        var creditDetail = data.data;
        var validCredit = Number(creditDetail.credit);
        var gameCredit = 0;
        var groupCredit = 0;
        if (creditDetail.gameCreditList && creditDetail.gameCreditList.length > 0) {
          creditDetail.gameCreditList.map(function (game) {
            if (game.validCredit && game.validCredit !== "unknown") {
              gameCredit += Number(game.validCredit);
            }
          });
        }

        if (creditDetail.lockedCreditList && creditDetail.lockedCreditList.length > 0) {
          creditDetail.lockedCreditList.map(function (group) {
            if (group.validCredit && group.validCredit !== "unknown") {
              groupCredit += Number(group.validCredit);
            }
          })
        }

        $rootScope.totalCredit = validCredit + gameCredit + groupCredit;
      }
    });

  }
})();

(function () {
  'use strict';

  angular
    .module('fishingKingMobile')
    .controller('InboxController', InboxController);

  /** @ngInject */
  function InboxController($rootScope, jQ, player) {
    var vm = this;
    vm.inboxData = [];
    vm.unreadInboxData = [];

    // sync read or sync unread messages from inbox data
    var syncUnreadMessages = function () {
      vm.inboxData.forEach(function(msg, i) {
        if(msg.hasBeenRead == false) {
          vm.unreadInboxData.push(msg);
        }
      })
    }
    var syncReadMessages = function () {
      vm.unreadInboxData.forEach(function(msg, i) {
        if(msg.hasBeenRead == true) {
          vm.unreadInboxData.splice(i, 1);
        }
      });
    }
    // add into OR remove from unread inbox data
    var addToUnreadInboxData = function (msg) {
      vm.unreadInboxData.push(msg);
    }
    var removeFromUnreadInboxData = function (_id) {
      vm.unreadInboxData.forEach(function(msg, i) {
        if(msg._id == _id) {
          vm.unreadInboxData.splice(i, 1);
        }
      });
    }
    var updateHasNewMail = function () {
      $rootScope.hasNewMail = vm.unreadInboxData.length > 0 ? true : false;
    }
    //sync unread inbox data from inbox data
    var syncUnreadInboxData = function () {
      syncUnreadMessages();
      syncReadMessages();
      updateHasNewMail();
    }

    // notify user of new mail
    var notifyNewMail = function (ev, data) {
      if(data && data.data) {
        vm.inboxData.push(data.data);
        if(data.data.hasBeenRead == false) {
          addToUnreadInboxData(data.data);
        }
      }
    }
    // deleteMail
    var deleteAllMail = function (readStatus) {
      var sendData = {};
      if (readStatus !== undefined) {
        sendData.hasBeenRead = Boolean(readStatus);
      }
      player.emit('deleteAllMail', sendData);
      player.on('deleteAllMail', function () {
        vm.getMail();
      });
    };

    vm.readMail = function (index) {
      if (!vm.inboxData[index].hasBeenRead) {
        var sendData = {mailObjId: ""};

        if (vm.inboxData && vm.inboxData[index] && vm.inboxData[index]._id) {
          sendData.mailObjId = vm.inboxData[index]._id;
        }
        player.emit('readMail', sendData);
        player.on('readMail', function (data) {
          vm.inboxData[index] = data.data;
          removeFromUnreadInboxData(data.data._id);
          updateHasNewMail();
        });
      }
    };

    vm.deleteMail = function (index) {
      var sendData = {mailObjId: ""};

      if (vm.inboxData && vm.inboxData[index] && vm.inboxData[index]._id) {
        sendData.mailObjId = vm.inboxData[index]._id;
      }
      player.emit('deleteMail', sendData);
      player.on('deleteMail', function (data) {
        if (data && data.data) {
          vm.inboxData.splice(index, 1);
        }
      });
    };
    vm.deleteAllMail = function () {
      deleteAllMail();
    };
    vm.deleteAllReadMail = function () {
      deleteAllMail(true);
    };
    vm.deleteAllUnreadMail = function () {
      deleteAllMail(false);
    };
    
    vm.getMail = function () {
      player.emit('getMailList', {});
      player.on('getMailList', function (data) {
        vm.inboxData = data.data;
        syncUnreadInboxData();
      });
    };
    vm.getUnreadMail = function () {
      player.emit('getUnreadMail', {});
      player.on('getUnreadMail', function (data) {
        vm.unreadInboxData = data.data;
      });
    };

    vm.getMail();
    $rootScope.$on('newMail', notifyNewMail);
  }
})();

