    vm.onlineTopupType = [];
    vm.bankTypeName = '';
    vm.userData = '';
    vm.paymentMessage = '';
    vm.showAlipayTopUp = false;
    vm.showWechatPayTopUp = false;
    vm.aliPayProposal = "";
